.partners {
  font-size: 0;
  background-color: @colorDark;
  background-image: url(../img/bg-dark.jpg);
}

.partners .container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.partners img {
  padding: 40px 0;
}

@media screen and (max-width: 1025px) {
  .partners .container {
    padding: 0 20px;
  }
}

@media screen and (max-width: 945px) {
  .partners {
    background-image: none;
  }
  
  .partners .container {
    justify-content: center;
  }
  
  .partners img {
    margin: 0 40px;
  }
}