@font-face {
  font-family: "ProximaNova";
  src: url("../fonts/ProximaNova-Regular.woff") format("woff"), url("../fonts/ProximaNova-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "ProximaNovaSemi";
  src: url("../fonts/ProximaNovaCond-Semibold.woff") format("woff"), url("../fonts/ProximaNovaCond-Semibold.woff") format("woff");
  font-weight: 600;
  font-style: bold;
  font-display: swap;
}
@font-face {
  font-family: "ProximaNova";
  src: url("../fonts/ProximaNova-Extrabold.woff") format("woff"), url("../fonts/ProximaNova-Extrabold.woff") format("woff");
  font-weight: 800;
  font-style: bold;
  font-display: swap;
}
@font-face {
  font-family: "ProximaNova";
  src: url("../fonts/ProximaNova-Bold.woff") format("woff"), url("../fonts/ProximaNova-Bold.woff") format("woff");
  font-weight: 800;
  font-style: bold;
  font-display: swap;
}
@font-face {
  font-family: "PTSerif";
  src: url("../fonts/PTSerif-Regular.woff") format("woff"), url("../fonts/PTSerif-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
body {
  font-size: 14px;
  font-family: "ProximaNova", "PTSerif", Georgia, Arial, sans-serif;
  color: #1f1f1f;
  position: relative;
}
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
*,
*::after,
*::before {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.container {
  position: relative;
  max-width: 1000px;
  margin: 0 auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.main {
  overflow: hidden;
}
/*base end*/
.header {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 5px 10px 0 35px;
  font-size: 15px;
  font-family: "ProximaNovaSemi";
  font-weight: 600;
}
.header .social-buttons {
  font-size: 0;
  margin-right: auto;
}
.header__phone {
  color: #1f1f1f;
  font-size: 17px;
}
.header__button {
  background-color: transparent;
  padding: 13px 40px;
  cursor: pointer;
  border: 1px solid transparent;
  outline: none;
  transition: 0.3s;
}
.header__button:hover {
  color: #b98348;
}
.header__button:focus {
  border: 1px solid #d7d7d7;
}
.header span {
  border-bottom: 1px dashed #868686;
  padding-left: 0;
  padding-right: 0;
  font-weight: 600;
}
.header__cart {
  margin-left: 5px;
}
.header__cart-quantity {
  font-family: "PTSerif";
  font-size: 14px;
  color: #b98348;
  border-left: 1px solid #b98348;
  position: absolute;
  top: 15px;
  right: 17px;
}
.header__cart .header__cart-quantity {
  padding-left: 5px;
  border-bottom: none;
}
.header__user {
  margin-left: 80px;
  padding: 13px 40px 13px 58px;
}
.header__phone {
  margin-left: 135px;
}
.header__geo {
  padding-left: 15px;
}
.header__geo,
.header__phone,
.header__user,
.header__cart {
  position: relative;
}
.header__geo::before,
.header__phone::before,
.header__user::before,
.header__cart::before {
  content: "";
  position: absolute;
  left: -15px;
  top: 3px;
}
.header__phone::before {
  content: url(../img/phone.png);
}
.header__user::before {
  content: url(../img/user.png);
  left: 36px;
  top: 14px;
}
.header__cart::before {
  content: url(../img/cart.png);
  left: 16px;
  top: 16px;
}
.header__geo::before {
  content: url(../img/geo.png);
  top: 1px;
  left: 0;
}
.header__geo::after {
  content: url(../img/arrow.png);
  margin-left: 3px;
}
@media screen and (max-width: 900px) {
  .header {
    justify-content: space-between;
  }
  .header__phone {
    margin-left: 30px;
  }
  .header .social-buttons {
    display: none;
  }
}
@media screen and (max-width: 650px) {
  .header {
    justify-content: space-between;
  }
  .header__user {
    margin-left: 0;
  }
}
@media screen and (max-width: 570px) {
  .header {
    justify-content: center;
    padding: 5px 40px 0;
  }
  .header__button {
    padding: 13px 40px 13px 40px;
  }
  .header__user::before {
    left: 20px;
  }
}
@media screen and (max-width: 355px) {
  .header {
    padding: 5px 10px 0;
  }
}
.nav {
  background-color: #1f1f1f;
}
.nav .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: uppercase;
  flex-wrap: wrap;
  padding-right: 20px;
  padding-left: 30px;
}
.nav__logo {
  margin: 10px 0;
}
.nav__button {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 18px;
  display: block;
  color: #ffffff;
  padding: 15px 0;
  background-color: transparent;
  border-color: transparent;
  display: none;
}
.nav__items {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
  display: flex;
}
.nav__item {
  margin-left: 50px;
}
.nav__item:first-child {
  margin-left: 0;
}
.nav__link {
  color: #ffffff;
  font-family: "ProximaNovaSemi";
  font-weight: 600;
  letter-spacing: 2px;
  padding: 5px 0;
}
.nav__link--active {
  border-bottom: 1px dashed #9c9c9c;
}
@media screen and (max-width: 910px) {
  .nav__button {
    display: block;
  }
  .nav__items {
    width: 300px;
    text-align: center;
    position: absolute;
    top: 62px;
    right: 0;
    flex-direction: column;
    background-color: #1f1f1f;
    z-index: 10;
    display: none;
  }
  .nav__item {
    margin-left: 0;
  }
  .nav__link {
    padding: 15px 0;
    display: block;
  }
  .nav__link--active {
    border-top: 1px dashed #9c9c9c;
  }
}
.main-baner {
  background-image: url(../img/main_image.jpg);
  background-repeat: no-repeat;
  padding: 100px 0;
}
.main-baner__text {
  max-width: 500px;
  font-family: "ProximaNova";
  font-size: 48px;
  line-height: 54px;
  margin: 0 25px;
  text-align: center;
  color: #ffffff;
}
.main-baner__text::after,
.main-baner__text::before {
  content: "";
  display: block;
  width: 170px;
  height: 3px;
  background-color: #ffffff;
}
.main-baner__text::after {
  margin: 40px auto 0;
}
.main-baner__text::before {
  margin: 0 auto 40px;
}
.main-baner__text-bold {
  font-weight: 800;
}
@media screen and (max-width: 910px) {
  .main-baner__text {
    width: 500px;
    margin: 0 auto;
  }
}
@media screen and (max-width: 545px) {
  .main-baner__text {
    font-size: 24px;
