    margin: 0 auto;
  }
}
.nav2__button {
  display: block;
  background-color: transparent;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 20px;
  padding: 15px 0;
  width: 160px;
  margin: 20px auto;
  box-shadow: inset rgba(0, 0, 0, 0.6) 0 -3px 8px, inset rgba(252, 255, 255, 0.7) 0 3px 8px;
  display: none;
}
.nav2__items {
  margin: 30px 0;
  padding: 10px 0;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  font-size: 14px;
}
.nav2__item a {
  padding: 10px;
  color: #1f1f1f;
  font-family: "ProximaNova";
  font-weight: 800;
  border: 2px solid transparent;
  transition: 0.3s;
}
.nav2__item a:hover {
  color: #b98348;
  border: 2px solid #b98348;
}
@media screen and (max-width: 840px) {
  .nav2__button {
    display: block;
  }
  .nav2__items {
    background-color: #ffffff;
    width: 300px;
    text-align: center;
    flex-direction: column;
    position: absolute;
    top: 20px;
    left: 50%;
    margin-left: -150px;
    z-index: 10;
    display: none;
  }
  .nav2__item a {
    display: block;
    padding: 15px 0;
  }
}
.blocks__items {
  margin: -5px;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.blocks__item {
  width: calc(33% - 10px);
  overflow: hidden;
  position: relative;
  margin: 5px;
}
.blocks__item--blog {
  overflow: visible;
  background-image: url(../img/108226fcffd8cd7a85729448be0265bc.jpg);
  background-size: cover;
}
.blocks__item--blog::before {
  content: "Блог";
  font-family: Georgia;
  font-style: italic;
  font-size: 16px;
  line-height: 16px;
  background-color: #b98348;
  color: #ffffff;
  display: block;
  padding: 7px 13px;
  position: absolute;
  top: 15px;
  right: -2px;
}
.blocks__link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  position: absolute;
  top: 0;
  border: 5px solid transparent;
  background: transparent;
  color: #ffffff;
  width: 100%;
  height: 100%;
  transition: 0.3s;
}
.blocks__link:hover {
  border: 5px solid #b98348;
  background: rgba(185, 131, 72, 0.3);
}
.blocks__text-head,
.blocks__text-brend {
  font-size: 20px;
  line-height: 20px;
}
.blocks__text-brend {
  font-family: Georgia;
}
.blocks__text-comment {
  font-size: 12px;
  line-height: 20px;
}
.blocks__text {
  background: rgba(185, 131, 72, 0.7);
  margin: 0 30px;
}
.blocks__text-p {
  margin: 0;
  font-size: 20px;
  font-weight: 600;
  line-height: 22px;
  padding: 5px;
  text-transform: none;
}
.blocks__link--shoes {
  align-items: flex-end;
}
.blocks__text-head--shoes {
  margin: 0;
}
.blocks__text-brend--shoes {
  line-height: 27px;
  margin-top: 10px;
  position: relative;
}
.blocks__text-brend--shoes::after,
.blocks__text-brend--shoes::before {
  content: "";
  display: block;
  width: 40px;
  height: 1px;
  background-color: #ffffff;
  position: absolute;
  top: 42px;
}
.blocks__text-brend--shoes::after {
  left: 55px;
}
.blocks__text-brend--shoes::before {
