  width: 190px;
  margin: 0 auto 40px;
  padding: 15px 0;
  border-top: 1px solid #ffffff;
  border-bottom: 1px solid #ffffff;
}
@media screen and (max-width: 930px) {
  .blocks__items {
    justify-content: space-around;
  }
  .blocks__item {
    width: calc(34% - 10px);
  }
}
@media screen and (max-width: 750px) {
  .blocks__items {
    justify-content: center;
