.form__block1 .form__input-time {
  width: 108px;
  padding: 13px 35px 12px;
}
.form__item {
  margin-bottom: 35px;
}
.form__time-wrap,
.form__name-wrap {
  margin-right: 40px;
}
.form__input-address,
.form__input-phone {
  width: 272px;
}
.form__item-address {
  margin-left: 40px;
}
.form__label {
  font-family: Georgia;
  font-size: 13px;
  font-style: italic;
  display: block;
}
.form__input {
  margin-top: 15px;
  padding: 13px 10px 12px 40px;
  border: 2px solid #1f1f1f;
  font-family: "ProximaNova";
  font-size: 15px;
  line-height: 15px;
  color: #b98348;
}
.form__input-name {
  width: 303px;
}
.form__button {
  width: 100%;
  padding: 15px;
  background-color: #b98348;
  color: #ffffff;
  border: transparent;
  box-shadow: 0px -3px 0px 0px #8b5c28 inset;
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  line-height: 18px;
}
.form__date-wrap,
.form__time-wrap,
.form__address-wrap,
.form__name-wrap,
.form__phone-wrap {
  position: relative;
}
.form__date-wrap::after,
.form__time-wrap::after,
.form__address-wrap::after,
.form__name-wrap::after,
.form__phone-wrap::after {
  content: "";
  position: absolute;
  top: 45px;
  left: 15px;
}
.form__date-wrap::after {
  content: url(../img/date.png);
}
.form__time-wrap::after {
  content: url(../img/time.png);
}
.form__address-wrap::after {
  content: url(../img/geo.png);
}
.form__name-wrap::after {
  content: url(../img/user.png);
}
.form__phone-wrap::after {
  content: url(../img/mobile.png);
}
.form__date-wrap::before,
.form__time-wrap::before,
.form__address-wrap::before {
  content: url(../img/arrow.png);
  position: absolute;
  top: 45px;
  right: 15px;
}
.form__date-wrap::before {
  right: 45px;
}
.form__time-wrap::before {
  right: 15px;
}
.form__input::placeholder {
  color: #b98348;
}
@media screen and (max-width: 1015px) {
  .form {
    height: auto;
  }
  .form .container {
    text-align: center;
  }
  .form__wrap {
    padding: 30px;
    position: relative;
  }
  .form__block1,
  .form__block2 {
    justify-content: center;
  }
  .form__time-wrap,
  .form__name-wrap {
    margin-right: 0;
  }
  .form__phone-wrap,
  .form__address-wrap {
    padding: 0 20px;
  }
  .form__address-wrap::after,
  .form__phone-wrap::after {
    left: 35px;
  }
  .form__address-wrap::before {
    right: 35px;
  }
}
@media screen and (max-width: 695px) {
  .form__block1 {
    margin-bottom: 5px;
  }
}
@media screen and (max-width: 395px) {
  .form__input-name {
    width: auto;
  }
  .form__input-date {
    margin-right: 0;
  }
  .form__input-time {
    margin: 15px 15px 0;
  }
  .form__time-wrap::before {
    right: 30px;
  }
  .form__time-wrap::after {
    left: 25px;
  }
}
.footer {
  background-color: #f6f6f6;
  border-top: 2px solid #ababab;
  border-bottom: 2px solid #ababab;
}
.footer .container,
.footer__catalog {
  display: flex;
  flex-wrap: wrap;
}
.footer .container {
  padding: 0 25px;
}
.footer__catalog {
  border-left: 2px solid #c3c3c3;
  border-right: 2px solid #c3c3c3;
  width: 455px;
}
.footer__nav {
  margin: 0;
  margin-right: 65px;
  padding-left: 0;
  list-style-type: none;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 600;
  line-height: 30px;
  padding: 50px 0 40px;
}
.footer__nav a {
  color: #b98348;
}
.footer__catalog .footer__nav {
  padding: 50px 0 40px 75px;
  margin-right: 15px;
}
.footer__catalog .footer__nav a {
  color: #4c4c4c;
  border-bottom: 1px solid #c3c3c3;
  font-size: 15px;
}
.footer__contacts {
  padding: 50px 30px 40px;
  font-size: 14px;
}
.footer__contacts-head {
  margin-top: 0;
  margin-bottom: 10px;
  font-weight: 600;
  line-height: 30px;
  color: #111111;
  text-transform: uppercase;
}
.footer__contacts-address,
.footer__contacts-mail,
.footer__contacts-tel {
  margin: 0;
  line-height: 20px;
}
.footer__contacts-tel span {
  font-weight: 600;
}
.footer__contacts-mail span {
  color: #b98348;
}
.footer .social-buttons {
  font-size: 0;
  margin-top: 22px;
}
.footer-small {
  background-color: #eeeeee;
}
.footer__copy {
  display: block;
  font-size: 12px;
  line-height: 12px;
  color: #828181;
  padding: 15px 25px;
}
@media screen and (max-width: 975px) {
  .footer .container {
    justify-content: center;
    padding: 0;
  }
  .footer__catalog {
    border: none;
    border-top: 2px solid #c3c3c3;
    border-bottom: 2px solid #c3c3c3;
    width: auto;
  }
  .footer .footer__nav {
    margin: 0;
    padding: 30px 50px;
  }
  .footer__contacts {
    padding: 30px;
    text-align: center;
  }
}
@media screen and (max-width: 465px) {
  .footer__catalog {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .footer__nav--2 {
    order: 1;
  }
}
.entrance {
  position: absolute;
  top: 50px;
  right: 10px;
  background-color: #ffffff;
  width: 265px;
  padding: 20px 25px;
  text-align: center;
  z-index: 10;
  box-shadow: 0 0 2px 1px #dddddd;
  display: none;
}
.entrance__name {
  font-family: Georgia;
  font-size: 14px;
  font-style: italic;
  line-height: 14px;
  color: #b98348;
  display: block;
  position: relative;
}
.entrance__name::after,
.entrance__name::before {
  content: "";
  display: block;
  width: 70px;
  height: 1px;
  background-color: #b98348;
  position: absolute;
  top: 7px;
}
.entrance__name::after {
  left: 10px;
}
.entrance__name::before {
  right: 10px;
}
.entrance__form {
  padding-top: 20px;
}
.entrance__input {
  width: 100%;
  padding: 12px 15px;
  margin-bottom: 18px;
  border: 1px solid #d7d7d7;
}
.entrance .form__button {
  font-size: 14px;
  font-family: "ProximaNova";
  font-weight: 400;
  line-height: 14px;
  letter-spacing: 1px;
  box-shadow: none;
}
.entrance__registration {
  font-family: Georgia;
  font-size: 14px;
  font-style: italic;
  line-height: 14px;
  color: #b98348;
  display: block;
  margin: 15px 0;
  position: relative;
}
.entrance__registration::after {
  content: "";
  display: block;
  width: 105px;
  height: 1px;
  background-color: #b98348;
  position: absolute;
  top: 15px;
  left: 55px;
}
.entrance .social-buttons {
  font-size: 0;
}
.entrance__registration-link {
  margin-right: 5px;
}
.entrance__registration-link:nth-child(5) {
  margin-right: 0;
}
.entrance span {
  border-bottom: none;
}
.entrance__input::placeholder {
  color: #c2c2c2;
  font-family: Georgia;
  font-size: 13px;
  font-style: italic;
  line-height: 18px;
}
.entrance__input:-ms-input-placeholder {
  color: #c2c2c2;
  font-family: Georgia;
  font-size: 13px;
  font-style: italic;
  line-height: 18px;
}
.entrance__input::-ms-input-placeholder {
  color: #c2c2c2;
  font-family: Georgia;
  font-size: 13px;
  font-style: italic;
  line-height: 18px;
}
@media screen and (max-width: 900px) {
  .entrance .social-buttons {
    display: block;
  }
}
@media screen and (max-width: 570px) {
  .entrance {
    top: 68px;
    right: 0;
  }
}
.cart {
  position: absolute;
  top: 50px;
  right: 0;
  background-color: #ffffff;
  width: 345px;
  padding: 20px 0;
  text-align: center;
  font-family: "ProximaNova";
  z-index: 10;
  box-shadow: 0 0 2px 1px #dddddd;
  display: none;
}
.cart span {
  border-bottom: none;
}
.cart__name,
.cart__total {
  font-family: Georgia;
  font-size: 14px;
  font-style: italic;
  line-height: 14px;
  color: #b98348;
  display: block;
  position: relative;
}
.cart__name::after,
.cart__total::after,
.cart__name::before,
.cart__total::before {
  content: "";
  display: block;
  width: 70px;
  height: 1px;
  background-color: #b98348;
  position: absolute;
  top: 7px;
}
.cart__name::after {
  left: 50px;
}
.cart__name::before {
  right: 50px;
}
.cart__total::after {
  left: 50px;
}
.cart__total::before {
  right: 50px;
}
.cart__items {
  margin: 20px 0;
  padding-left: 0;
  list-style-type: none;
}
.cart__item {
  display: flex;
  align-items: center;
  padding: 12px 40px 12px 0;
  transition: 0.3s;
}
.cart__item:hover {
  background-color: #f3f3f3;
}
.cart__del {
  border-color: transparent;
  background-color: transparent;
  cursor: pointer;
  padding: 0 10px;
}
.cart__del-wrap {
  position: relative;
}
.cart__del-wrap:hover::after {
  content: attr(data-title);
  position: absolute;
  top: -5px;
  left: -61px;
  z-index: 15;
  font-size: 11px;
  font-weight: 400;
  line-height: 18px;
  padding: 6px 0;
  color: #ffffff;
  width: 69px;
  height: 29px;
  background-image: url(../img/stiker_del.png);
  box-shadow: 0 0 2px 1px rgba(221, 221, 221, 0.1);
}
.cart__product {
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  text-align: left;
  margin: 0;
  padding-left: 12px;
  position: relative;
}
.cart__product::before {
  content: "";
  display: block;
  width: 1px;
  height: 20px;
  background-color: #c3c3c3;
  position: absolute;
  top: 4px;
  left: 0;
}
.cart__product span {
  display: block;
}
.cart__price {
  margin: 0 0 0 auto;
  font-size: 16px;
}
.cart__price span,
.cart__price-total span {
  font-size: 9px;
}
.cart__price,
.cart__product {
  padding-left: 12px;
  position: relative;
}
.cart__price::before,
.cart__product::before {
  content: "";
  display: block;
  width: 1px;
  height: 20px;
  background-color: #c3c3c3;
  position: absolute;
  left: 0;
}
.cart__price::before {
  top: 0;
}
.cart__product::before {
  top: 4px;
}
.cart__price-total {
  font-size: 20px;
  line-height: 20px;
}
.cart__button {
  width: 260px;
  display: block;
  margin: 50px auto 5px;
  padding: 12px 60px;
  background-color: #b98348;
  color: #ffffff;
  border: transparent;
  box-shadow: 0px -2px 0px 0px rgba(129, 86, 39, 0.75) inset;
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 18px;
}
@media screen and (max-width: 570px) {
  .cart {
    top: 68px;
  }
}
@media screen and (max-width: 365px) {
  .cart {
    width: 300px;
  }
  .cart__item {
    padding: 12px 5px 12px;
  }
}
