.blocks__items {
  margin: 0 - 5px;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.blocks__item {
  width: calc(33% - 10px);
  overflow: hidden;
  position: relative;
  margin: 5px;
}

.blocks__item--blog {
  overflow: visible;
  background-image: url(../img/108226fcffd8cd7a85729448be0265bc.jpg);
  background-size: cover;
  
  &::before {
    content: "Блог";
    font-family: Georgia;
    font-style: italic;
    font-size: 16px;
    line-height: 16px;
    background-color: @colorbrown;
    color: #ffffff;
    display: block;
    padding: 7px 13px;
    position: absolute;
    top: 15px;
    right: -2px;
  }
}

.blocks__link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  position: absolute;
  top: 0;
  border: 5px solid transparent;
  background: transparent;
  color: #ffffff;
  width: 100%;
  height: 100%;
  transition: 0.3s;
  
  &:hover {
    border: 5px solid @colorbrown;
    background: rgba(185, 131, 72, .3);
  }
}

.blocks__text-head,
.blocks__text-brend {
  font-size: 20px;
  line-height: 20px;
}

.blocks__text-brend {
  font-family: Georgia;
}

.blocks__text-comment {
  font-size: 12px;
  line-height: 20px;
}

.blocks__text {
  background: rgba(185, 131, 72, .7);
  margin: 0 30px;
}

.blocks__text-p {
  margin: 0;
  font-size: 20px;
  font-weight: 600;
  line-height: 22px;
  padding: 5px;
  text-transform: none;
}

.blocks__link--shoes {
  align-items: flex-end;
}

.blocks__text-head--shoes {
  margin: 0;
}

.blocks__text-brend--shoes {
  line-height: 27px;
  margin-top: 10px;
  position: relative;
  
  &::after,
  &::before  {
    content: "";
	display: block;
	width: 40px;
	height: 1px;
	background-color: #ffffff;
	position: absolute;
	top: 42px;
  }
  
  &::after {
    left: 55px;
  }
  
  &::before  {
    right: 55px;
  }
}

.blocks__text-brend--shoes span {
  display: block;
  font-family: "PTSerif";
  font-size: 22px;
  text-transform: none;
}

.blocks__text-button {
  padding: 20px;
  border: 2px solid #ffffff;
  font-weight: 600;
  margin-bottom: 35px;
}

.blocks__text-comment--cufflinks {
  padding: 0 65px;
}

.blocks__text-head--scarf {
  padding: 0 20px;
  margin-bottom: 10px;
  line-height: 25px;
}

.blocks__data {
  font-size: 12px;
  font-weight: 600;
  width: 190px;
  margin: 0 auto 40px;
  padding: 15px 0;
  border-top: 1px solid #ffffff;
  border-bottom: 1px solid #ffffff;
}

@media screen and (max-width: 930px) {
  .blocks__items {
    justify-content: space-around;
  }
  
  .blocks__item {
    width: calc(34% - 10px);
  }
}

@media screen and (max-width: 750px) {
  .blocks__items {
    justify-content: center;
	padding: 0 30px;
  }
  
  .blocks__item {
    width: calc(50% - 10px);
  }
}

@media screen and (max-width: 525px) {
  .blocks__items {
    padding: 0 5px;
  }
  
  .blocks__item {
    width: 300px;
	height: 300px;
  }
}