.entrance {
  position: absolute;
  top: 50px;
  right: 10px;
  background-color: #ffffff;
  width: 265px;
  padding: 20px 25px;
  text-align: center;
  z-index: 10;
  box-shadow: 0 0 2px 1px rgba(221, 221, 221, 1);
  display: none;
}

.entrance__name {
  font-family: Georgia;
  font-size: 14px;
  font-style: italic;
  line-height: 14px;
  color: @colorbrown;
  display: block;
  position: relative;
  
  &::after,
  &::before {
    content: "";
	display: block;
	width: 70px;
	height: 1px;
	background-color: @colorbrown;
	position: absolute;
	top: 7px;
  }
}

.entrance__name::after {
  left: 10px;
}

.entrance__name::before {
  right: 10px;
}

.entrance__form {
  padding-top: 20px;
}

.entrance__input {
  width: 100%;
  padding: 12px 15px;
  margin-bottom: 18px;
  border: 1px solid #d7d7d7;
}

.entrance .form__button {
  font-size: 14px;
  font-family: "ProximaNova";
  font-weight: 400;
  line-height: 14px;
  letter-spacing: 1px;
  box-shadow: none;
}

.entrance__registration {
  font-family: Georgia;
  font-size: 14px;
  font-style: italic;
  line-height: 14px;
  color: @colorbrown;
  display: block;
  margin: 15px 0;
  position: relative;
  
  &::after {
    content: "";
	display: block;
	width: 105px;
	height: 1px;
	background-color: @colorbrown;
	position: absolute;
	top: 15px;
	left: 55px;
  }
}

.entrance .social-buttons {
  font-size: 0;
}

.entrance__registration-link {
  margin-right: 5px;
}

.entrance__registration-link:nth-child(5) {
  margin-right: 0;
}

.entrance span {
  border-bottom: none;
}

.entrance__input::placeholder {
  color: #c2c2c2;
  font-family: Georgia;
  font-size: 13px;
  font-style: italic;
  line-height: 18px;
}

.entrance__input:-ms-input-placeholder {
  color: #c2c2c2;
  font-family: Georgia;
  font-size: 13px;
  font-style: italic;
  line-height: 18px;
}

.entrance__input::-ms-input-placeholder {
  color: #c2c2c2;
  font-family: Georgia;
  font-size: 13px;
  font-style: italic;
  line-height: 18px;
}

@media screen and (max-width: 900px) {
  .entrance .social-buttons {
    display: block;
  }
}

@media screen and (max-width: 570px) {
  .entrance {
    top: 68px;
	right: 0;
  }
}