.header {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding: 5px 10px 0 35px;
  font-size: 15px;
  font-family: "ProximaNovaSemi";
  font-weight: 600;
}

.header .social-buttons {
  font-size: 0;
  margin-right: auto;
  
}

.header__phone {
  color: @colorDark;
  font-size: 17px;
}

.header__button {
  background-color: transparent;
  padding: 13px 40px;
  cursor: pointer;
  border: 1px solid transparent;
  outline: none;
  transition: .3s;
  
  &:hover {
    color: @colorbrown;
  }
}  

.header__button:focus {
    border: 1px solid #d7d7d7;
}

.header span {
  border-bottom: 1px dashed #868686;
  padding-left: 0;
  padding-right: 0;
  font-weight: 600;
}

.header__cart {
  margin-left: 5px;
}

.header__cart-quantity {
  font-family: "PTSerif";
  font-size: 14px;
  color: @colorbrown;
  border-left: 1px solid @colorbrown;
  position: absolute;
  top: 15px;
  right: 17px;
}

.header__cart .header__cart-quantity {
  padding-left: 5px;
  border-bottom: none;
}

.header__user {
  margin-left: 80px;
  padding: 13px 40px 13px 58px;
}

.header__phone {
  margin-left: 135px;
}

.header__geo {
  padding-left: 15px;
}

.header__geo,
.header__phone,
.header__user,
.header__cart {
  position: relative;
  
  &::before {
    content: "";
	position: absolute;
	left: -15px;
	top: 3px;
  }
}

.header__phone::before {
  content: url(../img/phone.png);
}

.header__user::before {
  content: url(../img/user.png);
  left: 36px;
  top: 14px;
}

.header__cart::before {
  content: url(../img/cart.png);
  left: 16px;
  top: 16px;
}

.header__geo::before {
  content: url(../img/geo.png);
  top: 1px;
  left: 0;
}

.header__geo::after {
  content: url(../img/arrow.png);
  margin-left: 3px;
}

@media screen and (max-width: 900px) {
  .header {
    justify-content: space-between;
  }
  
  .header__phone {
    margin-left: 30px;
  }
  
  .header .social-buttons {
    display: none;
  }
}

@media screen and (max-width: 650px) {
  .header {
    justify-content: space-between;
  }
  
  .header__user {
    margin-left: 0;
  }
}

@media screen and (max-width: 570px) {
  .header {
    justify-content: center;
	padding: 5px 40px 0;
  }
  
  .header__button {
    padding: 13px 40px 13px 40px;
  }
  
  .header__user::before {
    left: 20px;
  }
}

@media screen and (max-width: 355px) {
  .header {
    padding: 5px 10px 0;
  }
}