.suit-order {
  background-image: url(../img/Big_image-2.jpg);
  background-repeat: no-repeat;
  margin-top: 10px;
  padding: 80px 0;
  color: #ffffff;
  text-align: center;
  font-size: 24px;
  font-weight: 600;
  font-style: italic;
  line-height: 38px;
  font-family: Georgia;
}

.suit-order__text {
  margin-top: 0;
  margin-bottom: 16px;
  
  &:after {
    content: "";
	display: block;
	width: 262px;
	height: 2px;
	background-color: #ffffff;
	margin: 25px auto 0;
  }
}

.suit-order__text span {
  display: block;
  text-transform: uppercase;
  font-family: "ProximaNova";
  font-style: normal;
  font-size: 21px;
}

.suit-order__text-4-5 {
  font-weight: 400;
}

.suit-order__text .suit-order__text-personally {
  font-weight: 900;
  font-size: 20px;
}

.suit-order__comment {
  font-weight: 400;
  font-size: 20px;
  margin: 0;
  
  &:after {
    content: "";
	background-image: url(../img/zig.png);
	background-repeat: no-repeat;
	display: block;
	width: 210px;
	height: 20px;
	margin: 25px auto 0;
  }
}

@media screen and (max-width: 380px) {
  .suit-order {
    padding: 80px 5px;
  }
  .suit-order .suit-order__text,
  .suit-order .suit-order__text-4-5,
  .suit-order .suit-order__text-personally  {
    font-size: 16px;
  }
}