.footer {
  background-color: #f6f6f6;
  border-top: 2px solid #ababab;
  border-bottom: 2px solid #ababab;
}

.footer .container,
.footer__catalog {
  display: flex;
  flex-wrap: wrap;
}

.footer .container {
  padding: 0 25px;
}

.footer__catalog {
  border-left: 2px solid #c3c3c3;
  border-right: 2px solid #c3c3c3;
  width: 455px;
}

.footer__nav {
  margin: 0;
  margin-right: 65px;
  padding-left: 0;
  list-style-type: none;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 600;
  line-height: 30px;
  padding: 50px 0 40px;
}

.footer__nav a {
  color: @colorbrown;
}

.footer__catalog .footer__nav {
  padding: 50px 0 40px 75px;
  margin-right: 15px;  
}

.footer__catalog .footer__nav a {
  color: #4c4c4c;
  border-bottom: 1px solid #c3c3c3;
  font-size: 15px;
}

.footer__contacts {
  padding: 50px 30px 40px;
  font-size: 14px;
}

.footer__contacts-head {
  margin-top: 0;
  margin-bottom: 10px;
  font-weight: 600;
  line-height: 30px;
  color: #111111;
  text-transform: uppercase;
}

.footer__contacts-address,
.footer__contacts-mail,
.footer__contacts-tel {
  margin: 0;
  line-height: 20px;
}

.footer__contacts-tel span {
  font-weight: 600;
}

.footer__contacts-mail span {
  color: @colorbrown;
}

.footer .social-buttons {
  font-size: 0;
  margin-top: 22px;
}

.footer-small {
  background-color: #eeeeee;
}

.footer__copy {
  display: block;
  font-size: 12px;
  line-height: 12px;
  color: #828181;
  padding: 15px 25px;
}

@media screen and (max-width: 975px) {
  .footer .container {
    justify-content: center;
	padding: 0;
  }
  
  .footer__catalog {
    border: none;
	border-top: 2px solid #c3c3c3;
	border-bottom: 2px solid #c3c3c3;
	width: auto;
  }
  
  .footer .footer__nav {
    margin: 0;
	padding: 30px 50px;
  }
  
  .footer__contacts {
    padding: 30px;
	text-align: center;
  }
}

@media screen and (max-width: 465px) {
  .footer__catalog {
    flex-direction: column;
	justify-content: center;
	align-items: center;
  }
  
  .footer__nav--2 {
    order: 1;
  }
}