.nav {
  background-color: @colorDark;
}

.nav .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: uppercase;
  flex-wrap: wrap;
  padding-right: 20px;
  padding-left: 30px;
}

.nav__logo {
  margin: 10px 0;
}

.nav__button {
  text-transform: uppercase;
  font-weight: 600;
  font-size: 18px;
  display: block;
  color: #ffffff;
  padding: 15px 0;
  background-color: transparent;
  border-color: transparent;
  display: none;
}

.nav__items {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
  display: flex;
}

.nav__item {
  margin-left: 50px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #ffffff;
  font-family: "ProximaNovaSemi";
  font-weight: 600;
  letter-spacing: 2px;
  padding: 5px 0;
}

.nav__link--active {
  border-bottom: 1px dashed #9c9c9c;
}

@media screen and (max-width: 910px) {
  .nav__button {
    display: block;
  }
  
  .nav__items {
    width: 300px;
	text-align: center;
    position: absolute;
	top: 62px;
	right: 0;
	flex-direction: column;
	background-color: @colorDark;
	z-index: 10;
	display: none;
  }
  
  .nav__item {
    margin-left: 0;
  }
  
  .nav__link {
	padding: 15px 0;
	display: block;
  }
  
  .nav__link--active {
    border-top: 1px dashed #9c9c9c;
  }
}