.nav2__button {
  display: block;
  background-color: transparent;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 20px;
  padding: 15px 0;
  width: 160px;
  margin: 20px auto;
  box-shadow: inset rgba(0,0,0,.6) 0 -3px 8px,
  inset rgba(252,255,255,.7) 0 3px 8px;
  display: none;
}

.nav2__items {
  margin: 30px 0;
  padding: 10px 0;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  font-size: 14px;
}

.nav2__item a {
  padding: 10px;
  color: @colorDark;
  font-family: "ProximaNova";
  font-weight: 800;
  border: 2px solid transparent;
  transition: .3s;
  
  &:hover {
    color: @colorbrown;
	border: 2px solid @colorbrown;
  }
}

@media screen and (max-width: 840px) {
  .nav2__button {
    display: block;
  }
  
  .nav2__items {
    background-color: #ffffff;
	width: 300px;
	text-align: center;
	flex-direction: column;
	position: absolute;
	top: 20px;
	left: 50%;
	margin-left: -150px;
	z-index: 10;
	display: none;
  }
  
  .nav2__item a {
    display: block;
	padding: 15px 0;
  }
}