.fitting {
  padding-top: 50px;
  padding-bottom: 120px;
  text-align: center;
  background-color: #eeeeee;
  background-image: url(../img/bg-brown.jpg);
}

.fitting__head {
  font-size: 28px;
  font-weight: 600;
  line-height: 38px;
  text-transform: uppercase;
  margin: 0;
}

.fitting__comment {
  font-size: 20px;
  line-height: 24px;
  margin: 10px;
  
  &:after {
    content: "";
	display: block;
	width: 170px;
	height: 2px;
	background-color: @colorDark;
	margin: 20px auto 0;
  }
}

@media screen and (max-width: 420px) {
  .fitting {
    padding-top: 20px;
  }
}