.form {
  background-color: #eeeeee;
  height: 340px;
}

.form__wrap {
  top: -105px;
  position: absolute;
  background-color: #ffffff;
  border: 2px solid @colorDark;
  padding: 45px 190px;
}

.form__block1,
.form__block2 {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 30px;
  position: relative;
}

.form__input-date {
  width: 165px;
  margin-right: 30px;
}

.form__block1 .form__input-time {
  width: 108px;
  padding: 13px 35px 12px;
}

.form__item {
  margin-bottom: 35px;
}

.form__time-wrap,
.form__name-wrap {
  margin-right: 40px;
}

.form__input-address,
.form__input-phone {
  width: 272px;
}

.form__item-address {
  margin-left: 40px;
}

.form__label {
  font-family: Georgia;
  font-size: 13px;
  font-style: italic;
  display: block;
}

.form__input {
  margin-top: 15px;
  padding: 13px 10px 12px 40px;
  border: 2px solid @colorDark;
  font-family: "ProximaNova";
  font-size: 15px;
  line-height: 15px;
  color: @colorbrown;
}

.form__input-name {
  width: 303px;
}

.form__button {
  width: 100%;
  padding: 15px;
  background-color: @colorbrown;
  color: #ffffff;
  border: transparent;
  box-shadow: 0px -3px 0px 0px #8b5c28 inset;
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  line-height: 18px;
}

.form__date-wrap,
.form__time-wrap,
.form__address-wrap,
.form__name-wrap,
.form__phone-wrap {
  position: relative;
  
  &::after {
    content: "";
	position: absolute;
	top: 45px;
	left: 15px;
  }
}

.form__date-wrap::after {
  content: url(../img/date.png);
}

.form__time-wrap::after  {
  content: url(../img/time.png);
}

.form__address-wrap::after  {
  content: url(../img/geo.png);
}

.form__name-wrap::after  {
  content: url(../img/user.png);
}

.form__phone-wrap::after  {
  content: url(../img/mobile.png);
}

.form__date-wrap,
.form__time-wrap,
.form__address-wrap {
  
  &::before {
    content: url(../img/arrow.png);
	position: absolute;
	top: 45px;
	right: 15px;
  }
}

.form__date-wrap::before {
  right: 45px;
}

.form__time-wrap::before {
  right: 15px;
}

.form__input::placeholder {
  color: @colorbrown;
}

@media screen and (max-width: 1015px) {
  .form {
    height: auto;
  }

  .form .container {
    text-align: center;
  }

  .form__wrap {
    padding: 30px;
	position: relative;
  }
  
  .form__block1,
  .form__block2 {
    justify-content: center;
  }
  
  .form__time-wrap,
  .form__name-wrap {
    margin-right: 0;
  }
  
  .form__phone-wrap,
  .form__address-wrap  {
    padding: 0 20px;
  }
  
  .form__address-wrap::after,
  .form__phone-wrap::after {
    left: 35px;
  }
  
  .form__address-wrap::before {
    right: 35px;
  }
}

@media screen and (max-width: 695px) {
  .form__block1 {
    margin-bottom: 5px;
  }
}

@media screen and (max-width: 395px) {
  .form__input-name {
    width: auto;
  }
  
  .form__input-date {
    margin-right: 0;
  }
  
  .form__input-time {
    margin: 15px 15px 0;
  }
  
  .form__time-wrap::before {
    right: 30px;
  }
  
  .form__time-wrap::after {
    left: 25px;
  }
}