.cart {
  position: absolute;
  top: 50px;
  right: 0;
  background-color: #ffffff;
  width: 345px;
  padding: 20px 0;
  text-align: center;
  font-family: "ProximaNova";
  z-index: 10;
  box-shadow: 0 0 2px 1px rgba(221, 221, 221, 1);
  display: none;
}

.cart span {
  border-bottom: none;
}

.cart__name,
.cart__total {
  font-family: Georgia;
  font-size: 14px;
  font-style: italic;
  line-height: 14px;
  color: @colorbrown;
  display: block;
  position: relative;
  
  &::after,
  &::before {
    content: "";
	display: block;
	width: 70px;
	height: 1px;
	background-color: @colorbrown;
	position: absolute;
	top: 7px;
  }
}

.cart__name::after {
  left: 50px;
}

.cart__name::before {
  right: 50px;
}

.cart__total::after {
  left: 50px;
}

.cart__total::before {
  right: 50px;
}

.cart__items {
  margin: 20px 0;
  padding-left: 0;
  list-style-type: none;
}

.cart__item {
  display: flex;
  align-items: center;
  padding: 12px 40px 12px 0;
  transition: .3s;
  
  &:hover {
    background-color: #f3f3f3;
  }
}

.cart__del {
  border-color: transparent;
  background-color: transparent;
  cursor: pointer;
  padding: 0 10px;
}

.cart__del-wrap {
  position: relative;
  
  &:hover::after {
    content: attr(data-title);
	position: absolute;
	top: -5px;
	left: -61px;
	z-index: 15;
	font-size: 11px;
	font-weight: 400;
	line-height: 18px;
	padding: 6px 0;
	color: #ffffff;
	width: 69px;
	height: 29px;
	background-image: url(../img/stiker_del.png);
	box-shadow: 0 0 2px 1px rgba(221, 221, 221, .1);
  }
}

.cart__product {
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
  text-align: left;
  margin: 0;
  padding-left: 12px;
  position: relative;
  
  &::before {
    content: "";
	display: block;
	width: 1px;
	height: 20px;
	background-color: #c3c3c3;
	position: absolute;
	top: 4px;
	left: 0;
  }
}

.cart__product span {
  display: block;
}

.cart__price {
  margin: 0 0 0 auto;
  font-size: 16px;
}

.cart__price span,
.cart__price-total span {
  font-size: 9px;
}

.cart__price,
.cart__product {
  padding-left: 12px;
  position: relative;
  
  &::before {
    content: "";
	display: block;
	width: 1px;
	height: 20px;
	background-color: #c3c3c3;
	position: absolute;
	left: 0;
  }
}

.cart__price::before {
  top: 0;
}

.cart__product::before {
  top: 4px;
}

.cart__price-total {
  font-size: 20px;
  line-height: 20px;
}

.cart__button {
  width: 260px;
  display: block;
  margin: 50px auto 5px;
  padding: 12px 60px;
  background-color: @colorbrown;
  color: #ffffff;
  border: transparent;
  box-shadow: 0px -2px 0px 0px rgba(129, 86, 39, .75) inset;
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 18px;
}

@media screen and (max-width: 570px) {
  .cart {
    top: 68px;
  }
}

@media screen and (max-width: 365px) {
  .cart {
   width: 300px;
  }
  
  .cart__item {
    padding: 12px 5px 12px;
  }
}