.main-baner {
  background-image: url(../img/main_image.jpg);
  background-repeat: no-repeat;
  padding: 100px 0;
}

.main-baner__text {
  max-width: 500px;
  font-family: "ProximaNova";
  font-size: 48px;
  line-height: 54px;
  margin: 0 25px;
  text-align: center;
  color: #ffffff;
  
  &::after,
  &::before  {
    content: "";
	display: block;
	width: 170px;
	height: 3px;
	background-color: #ffffff;
  }
  
  &::after {
    margin: 40px auto 0;
  }
  
  &::before {
    margin: 0 auto 40px;
  }
}

.main-baner__text-bold {
  font-weight: 800;
}

@media screen and (max-width: 910px) {
  .main-baner__text {
    width: 500px;
	margin: 0 auto;
  }
}

@media screen and (max-width: 545px) {
  .main-baner__text {
    font-size: 24px;
	width: 300px;
	margin: 0 auto;
  }
}